import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { TokenPayload, UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-login-form-admin',
  templateUrl: './login-form-admin.component.html',
  styleUrls: ['./login-form-admin.component.css', './prueba.css']
})
export class LoginFormAdminComponent implements OnInit {

  credentials: TokenPayload = {
    id: 0,
    name: '',
    email: '',
    password: '',
    rol_id: 0,
    empresa_id: 0
    };

    constructor(private auth: UserService, private router: Router) { }

  ngOnInit(): void {
  }

  login() {
    this.auth.login(this.credentials).subscribe(
      () =>
      {
        Swal.fire({
          icon: 'success',
          title: 'Ingresando...',
         });
        this.router.navigateByUrl('/admin/inicio');
      },
      err => {
        console.error(err);
        if (err.status === 0)
        {
          Swal.fire(
            'Error!',
            'El servidor no esta activo, no podra ingresar al sistema por favor contactar al Administrador...',
            'error'
          );
        }
        else if (err.status === 400)
        {
          Swal.fire(
            'Error!',
            '!Datos Incorrectos...',
            'error'
          );
        }
      }
    );
  }

}
