import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';

import { UserService } from 'src/app/services/user.service';
import { GeneralService } from '../../../../services/general.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-orden-compra-list-admin',
  templateUrl: './orden-compra-list-admin.component.html',
  styleUrls: ['./orden-compra-list-admin.component.css']
})
export class OrdenCompraListAdminComponent implements OnInit {

  ordenes: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { }

  ngOnInit(): void {
    this.informacionUsuario();
    this.getOrdens();
  }

  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  getOrdens()
  {
    this.generalService.getOrdens().subscribe(res => {
      this.ordenes = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }


}
