import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { HttpClient, HttpEventType } from '@angular/common/http';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';

@Component({
  selector: 'app-empresas-form-admin',
  templateUrl: './empresas-form-admin.component.html',
  styleUrls: ['./empresas-form-admin.component.css']
})
export class EmpresasFormAdminComponent implements OnInit {

  estados: any = [];
  empresas: any = [];

  edit = false;

  createFormGroup() {
    return new FormGroup({
      nombre_empresa: new FormControl('', [Validators.required]),
      telefono_empresa: new FormControl(''),
      direccion: new FormControl('', [Validators.required] ),
      estado_id: new FormControl('', [Validators.required]),
      url: new FormControl('', [Validators.required]),
           
   
    });
  }


   Form: FormGroup;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private generalService: GeneralService
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void {

    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    if (params.id)
          {
            this.generalService.getEmpresa(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.empresas = res;
                  this.Form.patchValue({
                    nombre_empresa: this.empresas.nombre_empresa,
                    telefono_empresa: this.empresas.telefono_empresa,
                    empresa_id: this.empresas.empresa_id,
                    direccion: this.empresas.direccion,
                    url: this.empresas.url,
                               
                    
               });

                  this.edit = true;
                 
                },
                err => console.log(err)
              );
        }
        this.getEstados();
  }


  Guardar() {

    // para excluir los campo cuando se vayan a guardar
    this.generalService.saveEmpresas(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.router.navigate(['/empresas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }

  Actualizar() {
  
    this.generalService.updateEmpresas(this.empresas.id, this.empresas)
      .subscribe(
        res => {
          console.log(res);
          this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }

    // COMBO LIST //
    getEstados()
    {
      this.generalService.getEstados().subscribe(res => {
        this.estados = res;
        console.log(res);
      },
      err => {
        console.log(err);
      }
     );
    }


}
