import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { HttpClient, HttpEventType } from '@angular/common/http';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';

@Component({
  selector: 'app-usuarios-form-admin',
  templateUrl: './usuarios-form-admin.component.html',
  styleUrls: ['./usuarios-form-admin.component.css']
})
export class UsuariosFormAdminComponent implements OnInit {

  usuario: any = [];
  empresas: any = [];
  roles: any = [];
  edit = false;



  createFormGroup() {
    return new FormGroup({
      name: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('' ),
      rol_id: new FormControl('', [Validators.required]),
      empresa_id: new FormControl('', [Validators.required]),
    
      
   
    });
  }


   Form: FormGroup;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private generalService: GeneralService
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void 
  {

    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    if (params.id)
          {
            this.userService.getUsuario(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.usuario = res;
                  this.Form.patchValue({
                    name: this.usuario.name,
                    email: this.usuario.email,
                    password: this.usuario.password,
                    rol_id: this.usuario.rol_id,
                    empresa_id: this.usuario.empresa_id,
             
                    
               });

                  this.edit = true;
                 
                },
                err => console.log(err)
              );
        }
       
        this.getEmpresas();
        this.getRoles();
  }


  Guardar() {

    // para excluir los campo cuando se vayan a guardar
    this.userService.register(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.router.navigate(['/inicio']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }

  Actualizar() {
  
    this.userService.updateUsuario(this.usuario.id, this.usuario)
      .subscribe(
        res => {
          console.log(res);
          this.router.navigate(['/inicio']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }

    // COMBO LIST //
    getEmpresas()
    {
      this.generalService.getEmpresas().subscribe(res => {
        this.empresas = res;
        console.log(res);
      },
      err => {
        console.log(err);
      }
     );
    }

    getRoles()
    {
      this.generalService.getRoles().subscribe(res => {
        this.roles = res;
        console.log(res);
      },
      err => {
        console.log(err);
      }
     );
    }






}
