import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { HttpClient, HttpEventType } from '@angular/common/http';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';




@Component({
  selector: 'app-orden-compra-form-admin',
  templateUrl: './orden-compra-form-admin.component.html',
  styleUrls: ['./orden-compra-form-admin.component.css']
})
export class OrdenCompraFormAdminComponent implements OnInit {

  ordenes: any = [];
  productos: any = [];

  edit = false;

  createFormGroup() {
    return new FormGroup({
      producto: new FormControl('', [Validators.required]),
      url: new FormControl('', [Validators.required]),
      empresa_id: new FormControl('', [Validators.required] ),
      descripcion: new FormControl('', [Validators.required]),
           
   
    });
  }


   Form: FormGroup;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private userService: UserService,
    private generalService: GeneralService
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void {

    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    if (params.id)
          {
            this.generalService.getProducto(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.productos = res;
                  this.Form.patchValue({
                    producto: this.productos.producto,
                    url: this.productos.url,
                    empresa_id: this.productos.empresa_id,
                    descripcion: this.productos.descripcion,
                               
                    
               });

                  this.edit = true;
                 
                },
                err => console.log(err)
              );
        }
  }


  Guardar() {

    // para excluir los campo cuando se vayan a guardar
    this.generalService.saveOrdens(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.router.navigate(['/productos/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }

  Actualizar() {
  
    this.generalService.updateOrdens(this.productos.id, this.productos)
      .subscribe(
        res => {
          console.log(res);
          this.router.navigate(['/productos/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }


}
