import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GeneralService } from 'src/app/services/general.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-productos-clientes-public-list',
  templateUrl: './productos-clientes-public-list.component.html',
  styleUrls: ['./productos-clientes-public-list.component.css']
})
export class ProductosClientesPublicListComponent implements OnInit {

  productos: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];


  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { }

  ngOnInit(): void {
    this.getProductos()
  }

  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  getProductos()
  {
    this.generalService.getProductos().subscribe(res => {
      this.productos = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }
}
