import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { InicioAdminComponent } from './components/admin/inicio/inicio-admin/inicio-admin.component';
import { EmpresasListAdminComponent } from './components/admin/empresas/empresas-list-admin/empresas-list-admin.component';
import { EmpresasFormAdminComponent } from './components/admin/empresas/empresas-form-admin/empresas-form-admin.component';
import { UsuariosListAdminComponent } from './components/admin/usuarios/usuarios-list-admin/usuarios-list-admin.component';
import { UsuariosFormAdminComponent } from './components/admin/usuarios/usuarios-form-admin/usuarios-form-admin.component';
import { LoginFormAdminComponent } from './components/public/login/login-form-admin/login-form-admin.component';
import { InicioPublicGeneralComponent } from './components/public/inicio-public-general/inicio-public-general.component';
import { ClientesLoginFormComponent } from './components/public/login/clientes-login-form/clientes-login-form.component';
import { InicioClientesPublicComponent } from './components/public/inicio-clientes-public/inicio-clientes-public.component';
import { ProductosClientesPublicListComponent } from './components/public/productos-clientes-public-list/productos-clientes-public-list.component';
import { ProductosListAdminComponent } from './components/admin/productos/productos-list-admin/productos-list-admin.component';
import { ProductosFormAdminComponent } from './components/admin/productos/productos-form-admin/productos-form-admin.component';


const routes: Routes = [

  { path: 'inicio', component:  InicioPublicGeneralComponent },

  //CLIENTES //

  { path: 'login/public/form', component:  ClientesLoginFormComponent },
  { path: 'login/admin/form', component:  LoginFormAdminComponent },
  { path: 'inicio/public/clientes', component:  InicioClientesPublicComponent },

  { path: 'inicio/public/productos', component:  ProductosClientesPublicListComponent },

  // FIN CLIENTES //

  //administrador //
  { path: 'admin/inicio', component:  InicioAdminComponent },

  { path: 'empresas/admin/list', component:  EmpresasListAdminComponent },
  { path: 'empresas/admin/form', component:  EmpresasFormAdminComponent },
  { path: 'empresas/admin/edit/:id', component:  EmpresasFormAdminComponent },

  { path: 'productos/admin/list', component:  ProductosListAdminComponent },
  { path: 'productos/admin/form', component:  ProductosFormAdminComponent },
  { path: 'productos/admin/edit/:id', component:  ProductosFormAdminComponent },

  { path: 'usuarios/admin/list', component:  UsuariosListAdminComponent },
  { path: 'usuarios/admin/form', component:  UsuariosFormAdminComponent },
  { path: 'usuarios/admin/edit/:id', component:  UsuariosFormAdminComponent },



  { path: 'login/admin/form', component:  LoginFormAdminComponent },

  { path: '**', pathMatch: 'full', redirectTo: 'inicio' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
