import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { API_URI, EmpresasInterfaces, OrdenCompraInterface, ProductosInterfaces } from '../interfaces/interface';


@Injectable({
  providedIn: 'root'
})
export class GeneralService {

  constructor(private http: HttpClient) { }

  getRoles() {
    return this.http.get(`${API_URI}/roles`);
  }

  getEstados() {
    return this.http.get(`${API_URI}/estados`);
  }


// EMPRESAS //
  
  getEmpresas() {
    return this.http.get(`${API_URI}/empresas`);
   
  }


  getEmpresa(id: string) {
    return this.http.get(`${API_URI}/empresas/${id}`);
  }

  saveEmpresas(Empresas: EmpresasInterfaces) {
    return this.http.post(`${API_URI}/empresas`,Empresas);
  }
  
  updateEmpresas(id: string|number, updatedEmpresas: EmpresasInterfaces): Observable<EmpresasInterfaces> {
    return this.http.put(`${API_URI}/empresas/${id}`, updatedEmpresas);
  }
  
  
  deleteEmpresas(id: string|number) {
    return this.http.delete(`${API_URI}/empresas/${id}`);
  }

  // AQUI TERMINA EMPRESAS //


  // Productos //
  
  getProductos() {
    return this.http.get(`${API_URI}/productos`);
   
  }


  getProducto(id: string) {
    return this.http.get(`${API_URI}/productos/${id}`);
  }

  saveProductos(Productos: ProductosInterfaces) {
    return this.http.post(`${API_URI}/productos`,Productos);
  }
  
  updateProductos(id: string|number, updatedProductos: ProductosInterfaces): Observable<ProductosInterfaces> {
    return this.http.put(`${API_URI}/productos/${id}`, updatedProductos);
  }
  
  
  deleteProductos(id: string|number) {
    return this.http.delete(`${API_URI}/productos/${id}`);
  }

  // AQUI TERMINA Productos //







  // Productos //
  
  getOrdens() {
    return this.http.get(`${API_URI}/orden_compra`);
   
  }


  getOrden(id: string) {
    return this.http.get(`${API_URI}/orden_compra/${id}`);
  }

  saveOrdens(Ordens: OrdenCompraInterface) {
    return this.http.post(`${API_URI}/orden_compra`,Ordens);
  }
  
  updateOrdens(id: string|number, updatedOrdens: OrdenCompraInterface): Observable<OrdenCompraInterface> {
    return this.http.put(`${API_URI}/orden_compra/${id}`, updatedOrdens);
  }
  
  
  deleteOrdens(id: string|number) {
    return this.http.delete(`${API_URI}/orden_compra/${id}`);
  }

  // AQUI TERMINA Productos //















}
